import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import { ProjectCard1 } from "./ProjectCard1";
import diceGameImg from "../assets/img/играКости.PNG";
import multiplicationGameImg from "../assets/img/играУмножения.PNG";
import searchMovieImg from "../assets/img/кинопоиск.PNG";
import weatherAppImg from "../assets/img/прогнозПогоды.PNG";
import clockImg from "../assets/img/часы.PNG";
import toursImg from "../assets/img/туры.PNG";
import menuImg from "../assets/img/меню.PNG";
import cartImg from "../assets/img/корзина.PNG";
import counterImg from "../assets/img/числовойСчетчик.PNG";
import colorSharp2 from "../assets/img/color-sharp2.png";
import "animate.css";
import TrackVisibility from "react-on-screen";

export const Projects = () => {
  const projects = [
    {
      title: "Цифровые часы",
      description: "Это часы с цифровой индикацией времени",
      imgUrl: clockImg,
      link: "https://kanatbayalbayev.github.io/digitalClock.github.io/",
    },
    {
      title: "КиноПоиск",
      description:
        "С помощью данного сервиса, Вы сможете найти любой фильм с его описанием онлайн",
      imgUrl: searchMovieImg,
      link: "https://kanatbayalbayev.github.io/movieApp.github.io/",
    },
    {
      title: "Игра в Умножения",
      description:
        "Игра в Умножения, в данной игре Вы должны умножить рандомные числа друг на друга и набрать как можно большое количество очков ",
      imgUrl: multiplicationGameImg,
      link: "https://kanatbayalbayev.github.io/multiplicationApp.github.io/",
    },
    {
      title: "Игра в кости",
      description:
        "Игра в кости, также известная как уличный крэпс, это несколько упрощенная версия той игры, в которую играют в казино",
      imgUrl: diceGameImg,
      link: "https://kanatbayalbayev.github.io/dicegame.github.io/",
    },
    {
      title: "Счетчик символов",
      description:
        "Данная программа поможет подсчитать количество слов и символов любого текста",
      imgUrl: counterImg,
      link: "https://kanatbayalbayev.github.io/realTimeCharacterCounter.github.io/",
    },
    {
      title: "Прогноз погоды",
      description:
        "Прогноз погоды в реальном времени позволяет мгновенно получать точную информацию о погоде независимо от того, где вы находитесь.",
      imgUrl: weatherAppImg,
      link: "https://kanatbayalbayev.github.io/weatherApp.github.io/",
    },
  ];
  const projects1 = [
    {
      title1: "Меню",
      description1: "проект Меню",
      imgUrl1: menuImg,
      link1: "https://github.com/KanatBayalbayev/menu-app-react",
    },
    {
      title1: "Корзина",
      description1: "проект Корзина",
      imgUrl1: cartImg,
      link1: "https://github.com/KanatBayalbayev/cart-app",
    },
    {
      title1: "Туры",
      description1: "проект Туры",
      imgUrl1: toursImg,
      link1: "https://github.com/KanatBayalbayev/tours-app",
    },
  ];

  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) => (
                <div
                  className={
                    isVisible ? "animate__animated animate__fadeIn" : ""
                  }
                >
                  <h2>Проекты</h2>

                  <Tab.Container id="projects-tabs" defaultActiveKey="first">
                    <Nav
                      variant="pills"
                      className="nav-pills mb-5 justify-content-center align-items-center"
                      id="pills-tab"
                    >
                      <Nav.Item>
                        <Nav.Link eventKey="first">Vanilla Javascript</Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="second">React</Nav.Link>
                      </Nav.Item>
                    </Nav>
                    <Tab.Content
                      id="slideInUp"
                      className={
                        isVisible ? "animate__animated animate__slideInUp" : ""
                      }
                    >
                      <Tab.Pane eventKey="first">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                      <Tab.Pane eventKey="second">
                        <Row>
                          {projects1.map((project, index) => {
                            return <ProjectCard1 key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                    </Tab.Content>
                  </Tab.Container>
                </div>
              )}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2}></img>
    </section>
  );
};
