import React from "react";
import Resume from "./cv.pdf";
import me from "../assets/img/me.jpg";

export const AboutMe = () => {
  return (
    <section className="about" id="about">
      <main class="site-main">
        <section class="about-area">
          <div class="container-fluid">
            <div class="row">
              <div class="col-lg-6 col-md-12">
                <div class="about-image">
                  <img src={me} alt="meImg" class="img-fluid" />
                </div>
              </div>
              <div class="col-lg-6 col-md-12 about-title">
                <h2 class="text-uppercase pt-5">
                  <span>Обо мне</span>
                </h2>
                <div class="paragraph py-4 w-75">
                  <p class="para">
                    Меня зовут Канат, я из города Алматы, являюсь self-taught
                    programmer. Освоил Frontend-разработку самостоятельно, и по
                    сей день осваиваю новые технологий, разные фрэймворки и
                    библиотеки для повышения своего coding skills. Опыт
                    разработки составляет 6 месяцев.
                  </p>
                  <p class="para">
                    На данный момент являюсь дата-аналитиком в Jusan банке, опыт
                    работы аналитиком составляет 1 год, но у меня есть огромное
                    желание развиваться как Frontend разробтчик и в дальнейшем
                    до расти до уровня Full-Stack разработчика.
                  </p>
                </div>
                <a href={Resume} download>
                  <button className="btn btn-success text-uppercase downloadCV">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="16"
                      height="16"
                      fill="currentColor"
                      class="bi bi-download"
                      viewBox="0 0 16 16"
                    >
                      <path d="M.5 9.9a.5.5 0 0 1 .5.5v2.5a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1v-2.5a.5.5 0 0 1 1 0v2.5a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2v-2.5a.5.5 0 0 1 .5-.5z" />
                      <path d="M7.646 11.854a.5.5 0 0 0 .708 0l3-3a.5.5 0 0 0-.708-.708L8.5 10.293V1.5a.5.5 0 0 0-1 0v8.793L5.354 8.146a.5.5 0 1 0-.708.708l3 3z" />
                    </svg>{" "}
                    Скачать CV
                  </button>
                </a>
              </div>
            </div>
          </div>
        </section>
      </main>
    </section>
  );
};
