import React, { useState } from "react";

export const Qualification = () => {
  const [toggleState, setToggleState] = useState(1);
  const toggleTab = (index) => {
    setToggleState(index);
  };

  return (
    <section className="qualification_section">
      <h2 className="section_title">Квалификация</h2>
      <div className="qualificaton_container container">
        <div className="qualifications_tabs">
          <div
            className={
              toggleState === 1
                ? "qualification_button qualification_active button--flex"
                : "qualification_button button--flex"
            }
            onClick={() => toggleTab(1)}
          >
            <i
              class="fas fa-user-graduate fa-beat"
              style={{ fontSize: "24px" }}
            ></i>{" "}
            Образование
          </div>
          <div
            className={
              toggleState === 2
                ? "qualification_button qualification_active button--flex"
                : "qualification_button button--flex"
            }
            onClick={() => toggleTab(2)}
          >
            <i
              class="far fa-address-card fa-beat"
              style={{ fontSize: "24px" }}
            ></i>{" "}
            Опыт работы
          </div>
        </div>
        <div className="qualification_sections">
          <div
            className={
              toggleState === 1
                ? "qualification_content qualification_content-active"
                : "qualification_content"
            }
          >
            <div className="qualification_data">
              <div>
                <h3 className="qualification_title">
                  Бакалавриат: Ресторанное дело и гостиничный бизнес
                </h3>
                <span className="qualification_subtitle">
                  Университет Туран
                </span>
                <div className="qualification_calender">
                  <i class="fa fa-calendar" style={{ fontSize: "16px" }}></i>{" "}
                  2015 - 2019
                </div>
              </div>
              <div>
                <span className="qualification_rounder"></span>
                <span className="qualification_line"></span>
              </div>
            </div>
            <div className="qualification_data">
              <div></div>
              <div>
                <span className="qualification_rounder"></span>
                <span className="qualification_line"></span>
              </div>
              <div>
                <h3 className="qualification_title">
                  Магистратура: Международный менеджмент предприятий
                </h3>
                <span className="qualification_subtitle">
                  Казахстанско-немецкий университет
                </span>
                <div className="qualification_calender">
                  <i class="fa fa-calendar" style={{ fontSize: "16px" }}></i>{" "}
                  2019 - 2021
                </div>
              </div>
            </div>
          </div>

          <div
            className={
              toggleState === 2
                ? "qualification_content qualification_content-active"
                : "qualification_content"
            }
          >
            <div className="qualification_data">
              <div>
                <h3 className="qualification_title">Менеджер по продажам</h3>
                <span className="qualification_subtitle">Альфа Банк</span>
                <div className="qualification_calender">
                  <i class="fa fa-calendar" style={{ fontSize: "16px" }}></i>{" "}
                  2020 сентябрь - 2021 сентябрь
                </div>
              </div>
              <div>
                <span className="qualification_rounder"></span>
                <span className="qualification_line"></span>
              </div>
            </div>
            <div className="qualification_data">
              <div></div>
              <div>
                <span className="qualification_rounder"></span>
                <span className="qualification_line"></span>
              </div>
              <div>
                <h3 className="qualification_title">
                  Специалист по анализу и отчетности{" "}
                </h3>
                <span className="qualification_subtitle">Альфа Банк</span>
                <div className="qualification_calender">
                  <i class="fa fa-calendar" style={{ fontSize: "16px" }}></i>{" "}
                  2021 октябрь - 2022 февраль
                </div>
              </div>
            </div>
            <div className="qualification_data">
              <div>
                <h3 className="qualification_title">
                  Ведущий специалист по анализу и отчетности{" "}
                </h3>
                <span className="qualification_subtitle">Банк ЦентрКредит</span>
                <div className="qualification_calender">
                  <i class="fa fa-calendar" style={{ fontSize: "16px" }}></i>{" "}
                  2022 март - 2022 май
                </div>
              </div>
              <div></div>
              <div>
                <span className="qualification_rounder_one"></span>
                <span className="qualification_line_one"></span>
              </div>
            </div>
            <div className="qualification_data">
              <div></div>
              <div>
                <span className="qualification_rounder"></span>
                <span className="qualification_line"></span>
              </div>
              <div>
                <h3 className="qualification_title">SQL-Аналитик данных</h3>
                <span className="qualification_subtitle">Жусан Банк</span>
                <div className="qualification_calender">
                  <i class="fa fa-calendar" style={{ fontSize: "16px" }}></i>{" "}
                  2022 июнь - по настоящее время
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
